import React, { useEffect } from 'react';
import { connect } from 'react-redux';
import { withRouter,Redirect } from 'react-router';
import { compose } from 'redux';
import Card from '../components/Card';
import { originalImgPath } from '../global/config';
import { setDiscoverData, setTvPageActionCreator} from "../redux/discover-reducer";
import {setTypeActionCreator } from "../redux/details-reducer";
import Pagination from '../components/Pagination';
const Tvpage = ({tvData, setDiscoverData, type, currentPage, setTvPageActionCreator}) => {

    useEffect(()=>{
        setDiscoverData('tv', currentPage)
    },[currentPage])
    useEffect(()=>{
        document.title = 'Сериалы'
    },[])
    return (
        <section>
            <div className="container-fluid">
            <h2 className="title">Сериалы</h2>
                <div className="main-grid">
                    {tvData && tvData.results.map(item=> {return <Card key={item.id} titleId={item.id} setType={setTypeActionCreator} type={'tv'} src={item.poster_path} title={item.title || item.name} score={item.vote_average}/>} )}
                </div>
                {tvData && <Pagination currentPage={currentPage} pagesCount={tvData.total_pages} setDiscoverData={setDiscoverData} discover='tv' setCurrentPage={setTvPageActionCreator}/>}
            </div>
            
        </section>
    );
};

const mapStateToProps = (state)=>{
    return{
        tvData: state.discover.tvData,
        currentPage: state.discover.tvPage,
        type: state.details.titleType,
    }
}
export default compose( 
    connect(mapStateToProps, {setDiscoverData, setTypeActionCreator, setTvPageActionCreator}),
    withRouter,
)(Tvpage)